.header {
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
  text-align: center;
  border-bottom: 1px solid black;
  font-size: 25px;
  font-weight: 600;
  background-color: rgba(128, 128, 128, 0.89);
}

.nav {
  position: relative;
  display: flex;
  gap: 30px;
  flex-grow: 0;
  align-items: center;
  padding-left: 16px;
  padding-right: 16px;
  min-height: 56px;
  height: 64px;
}

.active {
  text-decoration-line: none;
  color: blanchedalmond;
  list-style: none;
  font-size: 15;
  font-weight: 700;
}

.NavLink {
  color: black;
  list-style: none;
  text-decoration-line: none;
  font-size: 15;
  font-weight: 700;
}

.logout {
  align-items: center;
  color: black;
  font-size: 25px;
  font-weight: 600;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.logout:hover,
.logout:focus {
  color: blanchedalmond;
}
